.navbar {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    margin: 0;
    /* padding: 10px; */
    width: 100%;
    background-color: #ca5a04;
    position: static;
    overflow: auto;
}

.logomarca {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.logomarca h1 {
    color: #FFF;
    margin-left: 5px;
    -webkit-filter: drop-shadow(2px 2px 2px #121212);
    filter: drop-shadow(2px 2px 2px #121212);
}

.navbar div img {
    border-radius: 50%;
    display: block;
    margin: auto;
    width: 50px;
    height: 50px;
    -webkit-filter: drop-shadow(2px 2px 2px #121212);
    filter: drop-shadow(2px 2px 2px #121212);
    object-fit: cover;
}

.navbar a {
    display: block;
    color: #FFF;
    font-size: 16x;
    font-weight: 400;
    padding: 16px;
    text-decoration: none;
    display: flex;
    flex-direction: row;
    align-items: center;
    transition: ease-in-out .2s;
}


.navbar a:hover {
    color: #b3b3b3;
}


@media screen and (max-width: 700px) {
    .navbar {
        width: 100%;
        display: flex;
        flex-direction: column;
    }

   

    .content {
        display: flex;
        justify-content: center;
        width: 100%;
    }

}