.container {
    position: absolute;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background-color: #ca5a04;
    bottom: 0;
    width: 100%;
    height: 300px;
    /* padding: 3em; */
}

.logoArea {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 2em;
}

.logoArea h1 {
    color: #FFF;
    font-size: 50px;
    -webkit-filter: drop-shadow(2px 2px 2px #121212);
    filter: drop-shadow(2px 2px 2px #121212);
}

.logoArea img {
    padding: 20px;
    width: 150px;
    height: 150px;
    -webkit-filter: drop-shadow(2px 2px 2px #121212);
    filter: drop-shadow(2px 2px 2px #121212);
}

.socialmidia {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.socialmidia a {
    display: flex;
    flex-direction: row;
    align-items: center;
    text-decoration: none;
    color: #FFF;
    margin-left: 1em;
}

.socialmidia p {
    margin-left: .5em;
    font-weight: bold;
}

.copyright {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    position: sticky;
    margin-top: 1em;
    width: 100%;
    background-color: #ca5a04;
    border-top: #FFF solid 1px;
}


.copyright p {

    color: #FFF;
    margin-top: 10px;
    margin-right: .5em;
}